import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";

import App from "./components/App";
import lazyComponent from "./components/LazyComponent";
import Home from "./pages/Home";
import CONST_APP_ROUTES from "./constants/appRoutes";

export default function AppRoutes() {
  return (
    <BrowserRouter>
      <Routes>
        <Route path={CONST_APP_ROUTES.home.path} element={<App />}>
          <Route index element={<Home />} />
          {/* Blogs */}
          <Route path={CONST_APP_ROUTES.blogs.path} element={<Blogs />} />
          {/* Blogs::Travel */}
          <Route
            path={CONST_APP_ROUTES.blogsTravel.path}
            element={<BlogsTravelMap />}
          />
          <Route
            path={CONST_APP_ROUTES.blogsTravelWorldMap.path}
            element={<BlogsTravelWorldMap />}
          />
          {/* Tools */}
          <Route path={CONST_APP_ROUTES.tools.path} element={<Tools />} />
          {/* Tools::Crypto */}
          <Route
            path={CONST_APP_ROUTES.toolsCrypto.path}
            element={<ToolsCrypto />}
          />
          {/* Tools::Money */}
          <Route
            path={CONST_APP_ROUTES.toolsMoney.path}
            element={<ToolsMoney />}
          />
          <Route
            path={CONST_APP_ROUTES.toolsMoneyEx.path}
            element={<ToolsMoneyCurrencyEx />}
          />
          {/* Tools::Convert */}
          <Route
            path={CONST_APP_ROUTES.toolsConvert.path}
            element={<ToolsConvert />}
          />
          <Route
            path={CONST_APP_ROUTES.toolsConvertTemp.path}
            element={<ToolsConvertTemperature />}
          />
          {/* Tools::Time */}
          <Route
            path={CONST_APP_ROUTES.toolsTime.path}
            element={<ToolsTime />}
          />
          <Route
            path={CONST_APP_ROUTES.toolsTimeWorldClock.path}
            element={<ToolsTimeWorldClock />}
          />
        </Route>
      </Routes>
    </BrowserRouter>
  );
}

/**
 * Blogs
 */
const Blogs = lazyComponent("pages/Blogs");
/**
 * Blogs::Travel
 */
const BlogsTravelMap = lazyComponent("pages/Blogs/Travel");
const BlogsTravelWorldMap = lazyComponent("pages/Blogs/Travel/WorldMap");
/**
 * Tools
 */
const Tools = lazyComponent("pages/Tools");
/**
 * Tools::Convert
 */
const ToolsConvert = lazyComponent("pages/Tools/Convert");
const ToolsConvertTemperature = lazyComponent(
  "pages/Tools/Convert/Temperature"
);
/**
 * Tools::Crypto
 */
const ToolsCrypto = lazyComponent("pages/Tools/Crypto");
/**
 * Tools::Money
 */
const ToolsMoney = lazyComponent("pages/Tools/Money");
const ToolsMoneyCurrencyEx = lazyComponent("pages/Tools/Money/CurrencyEx");
/**
 * Tools::Time
 */
const ToolsTime = lazyComponent("pages/Tools/Time");
const ToolsTimeWorldClock = lazyComponent("pages/Tools/Time/WorldClock");
